
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.logo{
  width:12px;
}

h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  margin: 10px 0;
  text-align: left;
}
li input[type=text],
li input[type=number],
li input[type=tel] {
  height: 40px;
  padding-left: 15px;
  width: 70%;
  font-size: 16px;
}
a {
  color: #42b983;
}
.btn.green {
  background: #05c936da;
}
.btn.grey {
  background: #ccc;
}
.btn {
  background: #f5c800;
  border: 0;
  padding: 10px 30px;
  color: #fff;
  cursor: pointer;
}
table {
  width: 100%;
  margin: 20px 0;
}
table td {
  border: 1px solid #ddd;
  height: 50px;
}
.model {
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.model .box {
  width: 60%;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 30%;
  margin-left: -30%;
}
